import React from "react";
import { Button, Grid } from "@mui/material";
import { useNavigate } from "react-router-dom";

const MainPage = () => {
  const navigate = useNavigate();

  return (
    <Grid>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/linear-equation");
          }}
        >
          Równanie liniowe
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/square-equation");
          }}
        >
          Równanie kwadratowe
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/points-distance");
          }}
        >
          Odległość między punktami
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/arithmetic-average");
          }}
        >
          Średnia arytmetyczna
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/weighted-average");
          }}
        >
          Średnia ważona
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/bubble-sort");
          }}
        >
          Sortowanie bąbelkowe
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/quick-sort");
          }}
        >
          Sortowanie szybkie
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/calculator");
          }}
        >
          Kalkulator
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/triangle-calculations");
          }}
        >
          Trójkąt prostokątny- boki i kąty
        </Button>
      </Button>
    </Grid>
  );
};

export default MainPage;
